@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap&family=Roboto:wght@400;700&display=swap);
.row {
  position: relative;
  margin-bottom: 4rem;
}

.row-title {
  font-size: 24px;
  margin-bottom: 10px;
}

.card {
  margin-right: 5px;
  transition: all 0.3s ease 0.3s;
  position: relative;
  z-index: 1;
  cursor: pointer;
  left: 0;
}

.poster {
  width: 230px;
  height: auto;
  object-fit: contain;
  border-radius: 5px;
  transition-delay: 0.3s;
}

.movie-info {
  background-color: #222;
  padding: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateY(94%);
  border-radius: 0 0 5px 5px;
  visibility: hidden;
  transition-delay: 0.3s;
}

.card .poster-large + .movie-info {
  transform: translateY(0);
  background: linear-gradient(to top, rgba(1, 1, 1, 0.7), rgba(0, 0, 0, 0));
}

.card:hover {
  transform: scale(1.5);
  z-index: 3;
}

.card:nth-child(6n + 1):not(:nth-child(19)) {
  transform-origin: left !important;
}

.card:nth-child(6n + 6):not(:nth-child(18)),
.card:last-child {
  transform-origin: right !important;
}

.card:hover .poster {
  border-radius: 5px 5px 0 0;
}

.card:hover .poster-large {
  border-radius: var(--radius);
}

.card:hover > .movie-info {
  visibility: visible;
}

.card .title {
  font-size: 16px;
}

.rating {
  display: inline-block;
  background-color: var(--clr-red);
  padding: 5px;
  border-radius: var(--radius);
}

.rating {
  margin-top: 10px;
  font-size: 14px;
  font-weight: bold;
}

.rating .star {
  color: var(--clr-yellow);
}

.slider {
  position: absolute;
  top: 60%;
  transform: translateY(-50%);
  color: var(--clr-white);
  background-color: #11111154;
  height: 100%;
  width: 5%;
  border: none;
  font-size: 28px;
  z-index: 2;
  cursor: pointer;
}

.slider:focus {
  outline: none;
}

.prev {
  left: -5%;
}

.next {
  right: -5%;
}

.row .btn-container {
  visibility: hidden;
}

.row:hover > .btn-container {
  visibility: visible;
}

/* *::::: CUSTOM CSS MODAL-VIDEO */

.modal-video {
  position: absolute;
  top: 0;
  z-index: 5;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #111111cc;
}

.modal-video:focus {
  outline: none;
}

.modal-video-movie-wrap {
  padding: 0 !important;
  margin: 0 !important;
}

.modal-video-movie-wrap iframe {
  width: 800px;
  height: 450px;
}

.modal-video-close-btn {
  background: url(/static/media/x-square.4eabe0c8.svg) no-repeat center
    center/cover;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 20%;
  right: 15%;
  cursor: pointer;
  border: none;
  color: white;
}

.modal-video-close-btn:focus {
  outline: none;
}

.modal-video-close-btn:active {
  transform: scale(0.8);
}

/* *::::::: VARIABLE :::::: */
:root {
  --ff-primary: 'Poppins', sans-serif;
  --ff-secondary: 'Roboto', sans-serif;

  --clr-black: #111;
  --clr-black-light: #222;
  --clr-white: #f6f6f4;
  --clr-red: #e50914;
  --clr-yellow: #febe16;

  --radius: 5px;
  --transition: all 0.3s ease;
  --shadow: 0 7px 10px rgba(0, 0, 0, 0.1);
}

/* *::::::: GLOBAL STYLE :::::: */
*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Poppins', sans-serif;
  font-family: var(--ff-primary);
  color: #f6f6f4;
  color: var(--clr-white);
  background-color: #111;
  background-color: var(--clr-black);
  font-size: 18px;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 91.5vw;
  margin: 0 auto;
}

.btn {
  padding: 10px 25px;
  background-color: #f6f6f4;
  background-color: var(--clr-white);
  border: none;
  border-radius: 5px;
  border-radius: var(--radius);
  font-family: inherit;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 18px;
  cursor: pointer;
}

.btn:focus {
  outline: none;
}

.d-flex {
  display: flex;
}

.just-center {
  justify-content: center;
}

.just-between {
  justify-content: space-between;
}

.align-center {
  align-items: center;
}

.movies {
  overflow: hidden;
  padding-bottom: 5.5rem;
}

.banner {
  min-height: 85vh;
  display: flex;
  align-items: center;
}

.banner-content {
  width: 50%;
}

.banner .title {
  font-size: 48px;
}

.banner .desc {
  margin: 15px 0 30px;
}

.banner-icon {
  margin-right: 10px;
}

.btn.play {
  margin-right: 10px;
}

.btn.play:hover {
  background-color: #f6f6f4cc;
}

.btn.info {
  background-color: #eaeaea33;
  color: var(--clr-white);
}

.btn.info:hover {
  background-color: #eaeaea1a;
}

.fixed-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
  z-index: 999;
  transition: all 1s ease;
}

.fixed-nav .container {
  height: 70px;
}

.nav-logo {
  width: 100%;
  max-width: 100px;
}

.user-pic {
  width: 100%;
  max-width: 40px;
}

.fixed-nav.nav-bg {
  background-color: var(--clr-black);
  transition: all 1s ease;
}

.search {
  position: relative;
}

.search .input {
  background-color: var(--clr-white);
  border: none;
  padding: 1rem;
  font-size: 1rem;
  height: 3rem;
  width: 3rem;
  border-radius: var(--radius);
  transition: var(--transition) 0.3s;
}

.search .search-btn {
  border-radius: var(--radius);
  border: none;
  background-color: var(--clr-white);
  height: 3rem;
  width: 3rem;
  font-size: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  transition: var(--transition) 0.3s;
  cursor: pointer;
}

.search-btn:focus {
  outline: none;
}

.input:focus {
  outline: none;
}

.search.active .input {
  width: 15rem;
}

.search.active .search-btn {
  transform: translateX(14rem);
}

footer .container {
  padding: 0 100px 20px;
}

.sosmed {
  margin-bottom: 15px;
}

.sosmed li {
  font-size: 38px;
  margin-right: 30px;
  cursor: pointer;
  color: #777;
}

.footer-links {
  font-size: 14px;
}

.footer-links a {
  display: inline-block;
  color: #777;
  margin-bottom: 15px;
  cursor: pointer;
}

.footer-links a:hover {
  border-bottom: 1px solid #777;
  margin-bottom: 14px;
}

.footer-btn {
  display: inline-block;
  background-color: transparent;
  border: 1px solid #777;
  color: #777;
  font-size: 14px;
  font-weight: normal;
  border-radius: 0;
  margin: 20px 0;
  padding: 10px;
}

.footer-btn:hover {
  color: var(--clr-white);
}

.copyright {
  font-size: 12px;
  color: #777;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

